<template>
  <div class="home">
    <h1>这里是首页</h1>
    <button @click="closePage()"> 关闭</button>
    <div>
      <h2>评分入口：</h2>
      <router-link to="/scoring">scoring</router-link>
    </div>
    <div>
      <h2>质量体验报告入口：</h2>
      <router-link to="./quality-report">quality-report</router-link>
    </div>
    <div>
      <button @click="timeConsume">查询耗时数据</button>
     
      房间时长：{{  fangjianhaoshi}}s<br/>
      互动时长：{{  timeInteract}}s<br/>
       端到端延迟{{ peer_to_peer_time }}ms<br/>
       这次登录耗时：{{ processing_time }}ms<br/>
    </div>
    <div>
      <button @click="timeInteractiveStart">查询互动时长</button>
    </div>
    <br />
    <div>
      <button @click="breakRateEnd2endDelay">查询端到端延迟breakRateEnd2endDelay</button>
    </div>
    <br />
    <div>
      <button @click="errorEvent">查询错误事件数据</button>
    </div>
    <br />
    <div>
      <button @click="DeviceInfo">查询DeviceInfo数据</button>
    </div>
    <br />
    <div>
      <button @click="cpuUsage">查询cpuUsage数据</button>
    </div>
    <br />
    <div>
      <button @click="pushBitRate">查询pushBitRate数据</button>
    </div>
    <br />
    <div>
      <button @click="pullBitRate">查询pullBitRate数据</button>
    </div>
    <br />
    <div>
      <button @click="frameRate">查询frameRate数据</button>
    </div>
  </div>
</template>
<script>
import { JsBridge } from "../../utils/jsBridge";
// import {deviceCpuInfoData,handleChartData} from '../../utils/dataSource';
export default {
  name: "home",
  data() {
    return {
      uid: "",
      room_id: "",
      // 房间type=1的对应耗时
      processing_time: 0,
      // 房间开始时间
      startTimeProcessing_time:0,
      // 房间结束时间
      endTimeProcessing_time:0,
      // 房间耗时
      fangjianhaoshi:0,
      // 互动时长
      timeInteract:0,
      //端到端延迟
      peer_to_peer_time:0,
      deviceCpuInfoData:[],
      cpuUsageData:[],
      pushBitRateData:[],
      pullBitRateData:[],
      frameRateData:[],
      // 首帧
      firstFrameList:[],
      firstFrameTime:0
    };
  },
  async created() {
    JsBridge.init();
    let query = window.location.search.substring(1);
    let vars = query.split("&");
    for (let i = 0; i < vars.length; i++) {
      let pair = vars[i].split("=");
      if (pair[0] == "uid") {
        this.uid = pair[1];
      } else if (pair[0] == "room_id") {
        this.room_id = pair[1];
      }
    }
    // console.log("uid", this.uid);
    // console.log("room_id", this.room_id);

    //查询登陆耗时
    await this.timeConsume();
    // console.log("进房耗时：ms", this.processing_time);
    // console.log('deviceCpuInfoData',deviceCpuInfoData);
  // const res =   handleChartData(deviceCpuInfoData)
  },

  components: {},
  methods: {
    closePage(){
      console.log('我要关闭了');
      JsBridge.closePage();
    },
    async timeConsume() {
      let res = await JsBridge.timeConsume(this.uid, this.room_id);
      // console.log("timeConsume", res);
      let i = 0;
      // console.log('res.length',res.length);
      // console.log('i2324',res[0]);
      for (i = 0; i < res.length; i++) {
        if (res[i].type === '1') {
          this.processing_time = res[i].processing_time;
          this.startTimeProcessing_time = res[i].timestamp;
          // console.log('开始时间,开始时间戳',this.processing_time,this.startTimeProcessing_time);
        }
        if(res[i].type === '2'){
          this.endTimeProcessing_time = res[i].timestamp;
          // console.log('endTimeProcessing_time',this.endTimeProcessing_time);
        }
        this.fangjianhaoshi = this.endTimeProcessing_time- this.startTimeProcessing_time;
      }
      // console.log(' this.fangjianhaoshi', this.fangjianhaoshi);
      this.firstFrameList = res.map(item=>{
        if(item.type==='5'){
          if(item.processing_time){
            return item;
          }
          // 数组item 会有undefined
        }
      })
      // console.log('firstFrameList',this.firstFrameList);
      for(let i=0;i<this.firstFrameList.length;i++){
        // let time_all = 0;
        // time_all = time_all+ this.firstFrameList[i].processing_time;
        // this.firstFrameTime = time_all / this.firstFrameList.length
      }
    },
    async timeInteractiveStart() {
      // 查询互动时长
      let res = await JsBridge.timeInteractiveStart(this.uid, this.room_id);
      this.timeInteract = res[1].timestamp-res[0].timestamp;
      // console.log("timeInteractiveStart", res[1].timestamp-res[0].timestamp,":s");
    },
    async breakRateEnd2endDelay() {
      let res = await JsBridge.breakRateEnd2endDelay(this.uid, this.room_id);
      // this.peer_to_peer_time =res.map[]
      // this.peer_to_peer_time 
      let sum = 0;
      for(let i=0;i<res.length;i++){
        // debugger;
        
        // console.log('ityem:',res[i]);
        sum = sum +(res[i].peer_to_peer_delay * 1);
      }
      this.peer_to_peer_time = Math.round(sum/res.length);
      // console.log("端到端延迟数据breakRateEnd2endDelay", res);
      
    },
    async errorEvent() {
      let res = await JsBridge.errorEvent(this.uid, this.room_id);
      console.log("errorEvent", res);
    },
    async DeviceInfo() {
      let res = await JsBridge.DeviceInfo(this.uid, this.room_id);
      // console.log("DeviceInfo", res);
      this.deviceCpuInfoData = res;
      console.log('所有的设备信息',this.deviceCpuInfoData);
    }, 
    // 系统占用率
    async cpuUsage() {
      let res = await JsBridge.cpuUsage(this.uid, this.room_id);
      // console.log("cpuUsage", res);
      this.cpuUsageData=res;
      console.log('系统占用率(cpu_usage_app,cpu_usage_sys,timestemp)',this.cpuUsageData);
      
    },
    async pushBitRate() {
      let res = await JsBridge.pushBitRate();
      // console.log("pushBitRate", res);

      this.pushBitRateData = res;
      // console.log('pushBitRateData',this.pushBitRateData);
    },
    // pullBitRate
    async pullBitRate() {
      let res = await JsBridge.pullBitRate(this.uid, this.room_id);
      // console.log("pullBitRate", res);

      this.pullBitRateData = res;
      console.log('pullBitRat2222e',this.pullBitRateData);
    },
    // 
    async frameRate() {
      let res = await JsBridge.frameRate(this.uid, this.room_id);
      // console.log("frameRate", res);
      this.frameRateData = res;
      // console.log('frameRateData',this.frameRateData);
    },
  },
};
</script>
