export class JsBridge {
       static seq = 0;
       static callBackMap = {};

       // 1 Windows    2 MacOS    4 iOS     8 Android      16 小程序     32 Web     64 Electron
       static platform = 4;

       static getSeq() {
              return ++JsBridge.seq;
       }
       static closePage(){
              window.webkit.messageHandlers.closePage.postMessage({})
       }
       static search(table_name, sql, callBack) {


              if (!window.quality && !window.webkit) {
                     console.error('env is not supported');
                     return false;
              }

              let seq = JsBridge.getSeq();
              JsBridge.callBackMap[seq] = {
                     callBack,
                     time: new Date().getTime()
              }
              // eslint-disable-next-line 
              // console.log('call search:', sql, myPlatForm);
              // eslint-disable-next-line 
              if (JsBridge.platform === 4 ) {
                     console.log(seq, sql, table_name);
                     window.webkit.messageHandlers.search.postMessage({ seq, sql, table_name })
                     // eslint-disable-next-line 
              } else if (JsBridge.platform === 8 ) {
                     window.quality.search(seq, table_name, sql);
              } else {
                     console.error('platform is known');
              }

       }

       static init(platform) {
              console.warn('jsBridge init');
              let callBack = (seq, result) => {
                     console.log('jsBridge callback:', seq, result)
                     if (JsBridge.callBackMap[seq]) {
                            JsBridge.callBackMap[seq].callBack(result);
                            delete JsBridge.callBackMap[seq];
                     } else {
                            console.error('jsBridge callback seq not founded!!')
                     }

              }
              window.quality_callback = callBack;
              JsBridge.startTimer();
              JsBridge.platform = platform;
       }

       static startTimer(){
              setTimeout(()=>{
                     for(let key in JsBridge.callBackMap){
                        if(Date.now() - JsBridge.callBackMap[key].time > 3000){
                            console.error('timeOut');
                            JsBridge.callBackMap[key].callBack(undefined);
                            delete JsBridge.callBackMap[key];
                        }
                    }
                    JsBridge.startTimer();
              },3000);
       }

       // static timeInteractive(userID, roomID) {
       //        return new Promise((res) => {
       //               JsBridge.search('UserEvent', `SELECT min(timestamp) FROM UserEvent WHERE type == '3' and user_id = '${userID}' and room_id = '${roomID}' UNION SELECT max(timestamp) FROM UserEvent WHERE type == '4' and user_id = '${userID}' and room_id = '${roomID}'`, (result) => {
       //                      res(result);
       //               })
       //        });

       // }
       // 互动时长 对了：res[1].timestamp-res[0].timestamp  就是互动时长
       static timeInteractiveStart(userID, roomID) {
              return new Promise((res) => {
                     JsBridge.search('UserEvent', `SELECT timestamp , min(timestamp) FROM UserEvent WHERE (type == '3' OR type =='5') and user_id = '${userID}' and room_id = '${roomID}' UNION SELECT timestamp , max(timestamp) FROM UserEvent WHERE (type == '4' OR type == '6') and user_id = '${userID}' and room_id = '${roomID}'`, (result) => {
                            res(result);
                     })
              });

       }
       // 耗时(登录耗时：processing_time)、房间时长(对了：this.fangjianhaoshi)、首帧耗时（有问题：type=5 且有processing_time 的所有数据加起来/this.firstFrameList.length）
       static timeConsume(userID, roomID) {
              return new Promise((res) => {
                     JsBridge.search('UserEvent', `SELECT type, timestamp, processing_time FROM UserEvent WHERE (type == '1' OR type == '2' OR type == '3'  OR type == '5') AND (user_id == '${userID}' and room_id == '${roomID}')`, (result) => {
                            res(result);
                     })
              });

       }
       // 端到端延迟（拿到了数据了，this.peer_to_peer_time :ms）
       static breakRateEnd2endDelay(userID, roomID) {
              return new Promise((res) => {
                     JsBridge.search('StreamQuality', `SELECT peer_to_peer_delay FROM StreamQuality WHERE user_id == '${userID}'  and room_id == '${roomID}' AND peer_to_peer_delay > 0`, (result) => {
                            res(result);
                     })
              });

       }  
       // 查看异常事件--首帧耗时大于5000 type=5 的processing_time>5000 就是首帧耗时耗时>5s；登录耗时：type=1 的processing_time>5000 就是登录房间耗时>5s
       static errorEvent(userID, roomID) {
              // SELECT type, timestamp FROM UserEvent WHERE (room_id == '${roomID}' and user_id = '${userID}') AND (processing_time > 5000 or error_code != '0') ORDER BY timestamp ASC
              return new Promise((res) => {
                     JsBridge.search('UserEvent', `SELECT type, timestamp FROM UserEvent WHERE (room_id == '${roomID}' and user_id = '${userID}') AND (processing_time > 5000 or error_code != '0') ORDER BY timestamp ASC`, (result) => {
                            res(result);
                     })
              });
       }
       // 查看内存使用量
       static DeviceInfo(userID, roomID) {
              return new Promise((res) => {
                     JsBridge.search('DeviceInfo', `SELECT  memory_device, memory_usage_app, memory_usage_sys ,timestamp FROM DeviceInfo WHERE user_id == '${userID}' and room_id == '${roomID}' `, (result) => {
                            res(result);
                     })
              });

       } 

       static  cpuUsage(userID, roomID) {
              return new Promise((res) => {
                     JsBridge.search('DeviceInfo', `SELECT cpu_usage_app, cpu_usage_sys, timestamp FROM DeviceInfo WHERE  user_id == '${userID}' and room_id == '${roomID}'`, (result) => {
                            res(result);
                     })
              });

       }
       //拉流信息SELECT video_frame_rate, audio_frame_rate, video_bit_rate, audio_bit_rate, timestamp, stream_id_push FROM StreamQuality WHERE stream_id_push > 0 and user_id == '${userID}' and room_id == '${roomID}'
       
       // 推流码率(可以拿到)
       ///-----记得改回uid
       static  pushBitRate(userID,roomID) {
              return new Promise((res) => {
                     JsBridge.search('StreamQuality', `SELECT video_frame_rate, audio_frame_rate, video_bit_rate, audio_bit_rate, timestamp, stream_id_push FROM StreamQuality WHERE stream_id_push > 0 user_id == '${userID}' and room_id == '${roomID}' ORDER BY stream_id_play`, (result) => {
                            res(result);
                     })
              });

       }
       // 拉流码率（可以拿到）
       static pullBitRate(userID,roomID){
              return new Promise((res) => {
                     JsBridge.search('StreamQuality', `SELECT video_frame_rate, audio_frame_rate, video_bit_rate, audio_bit_rate, timestamp, stream_id_play FROM StreamQuality WHERE stream_id_play > 0 and user_id == '${userID}' and room_id == '${roomID}' ORDER BY stream_id_play`, (result) => {
                            res(result);
                     })
              });
       }
       // 帧率：（10:02）
       static  frameRate(userID, roomID) {
              return new Promise((res) => {
                     JsBridge.search('StreamQuality', `SELECT video_frame_rate, audio_frame_rate, timestamp, stream_id_push, stream_id_play FROM StreamQuality WHERE  user_id == '${userID}' and room_id == '${roomID}' 
                     `, (result) => {
                            res(result);
                     })
              });

       }


}
