export const handleChartData =  function (deviceCpuInfoData) {

    let xAxis = [],cpu_usage_app = [],cpu_usage_sys = []
    deviceCpuInfoData.forEach(item=>{
        xAxis.push(item.timestamp);
        cpu_usage_app.push(item.cpu_usage_app);
        cpu_usage_sys.push(item.cpu_usage_sys);
     
     });
     return { xAxis,cpu_usage_app,cpu_usage_sys }

}
