export const handleRateChartData =  function (PushbiteRateData) {

    let xAxis = [],video_frame_rate = [],audio_frame_rate = [],video_bit_rate=[],audio_bit_rate=[];
    PushbiteRateData.forEach(item=>{
        xAxis.push(item.timestamp);
        // item.video_frame_rate = -item.video_frame_rate;
        // item.audio_frame_rate =-item.audio_frame_rate;
        // item.video_bit_rate = -video_bit_rate;
        // audio_bit_rate = -audio_bit_rate;
        video_frame_rate.push(item.video_frame_rate);
        audio_frame_rate.push(-item.audio_frame_rate);
        video_bit_rate.push(item.video_bit_rate);
        audio_bit_rate.push(-item.audio_bit_rate);
     
     });
     return { xAxis,video_frame_rate,audio_frame_rate,video_bit_rate,audio_bit_rate }

}
    
