import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home'
import Scoring from '@/components/scoring'
import QualityReport from '@/components/qualityReport'

Vue.use(Router)

const router = new Router({
  routes: [
    // 首页
    {
      path: '/',
      // redirect: '/scoring',
      // redirect: '/quality-report',
      name: 'home',
      component: Home,
      meta: {
        title: 'home'
      }
    },
    {
      path: '/scoring',
      name: 'scoring',
      component: Scoring,
      meta: {
        title: 'scoring'
      }
    },
    {
      path:'/quality-report',
      name: 'quality-report',
      component: QualityReport,
      meta: {
        title: '质量报告'
      }
    }
  ]
})

export default router