    // 房间耗时
    async timeConsume() {
      let res = await JsBridge.timeConsume(this.uid, this.room_id);
      // console.log("timeConsume", res);
      let i = 0;
      // console.log('i2324',res[0]);
      for (i = 0; i < res.length; i++) {
        if (res[i].type === '1') {
          this.processing_time = res[i].processing_time;
          this.startTimeProcessing_time = res[i].timestamp;
          // console.log('登录（进房）时间,开始时间戳',this.processing_time,this.startTimeProcessing_time);
          this.roomInfoList[4].value = this.processing_time;
        }
        if(res[i].type === '2'){
          this.endTimeProcessing_time = res[i].timestamp;
          // console.log('endTimeProcessing_time',this.endTimeProcessing_time);
        }
        this.fangjianhaoshi = this.endTimeProcessing_time- this.startTimeProcessing_time;
        this.roomInfoList[0].value = this.fangjianhaoshi;
      }
      // console.log(' this.fangjianhaoshi', this.fangjianhaoshi);
      this.firstFrameList = res.map(item=>{
        if(item.type==='5'){
          if(item.processing_time){
            return item;
          }
          // 数组item 会有undefined
        }
      })
      // console.log('firstFrameList',this.firstFrameList);
      for(let i=0;i<this.firstFrameList.length;i++){
        // (res[i].peer_to_peer_delay * 1)
        let time_all = 0;
        time_all = time_all+ (this.firstFrameList[i].processing_time)*1;
        this.firstFrameTime = Math.round(time_all / this.firstFrameList.length);
        // console.log('首帧耗时：',this.firstFrameTime);
        this.roomInfoList[3].value = this.firstFrameTime;
      }
    },
    // 互动时长
