      // 查询互动时长
      let res = await JsBridge.timeInteractiveStart(this.uid, this.room_id);
      this.timeInteract = res[1].timestamp-res[0].timestamp;
      this.roomInfoList[1].value = this.timeInteract;
      // console.log("timeInteractiveStart", res[1].timestamp-res[0].timestamp,":s");
    },
    // 端到端延迟
    async breakRateEnd2endDelay() {
      let res = await JsBridge.breakRateEnd2endDelay(this.uid, this.room_id);
      // this.peer_to_peer_time =res.map[]
      // this.peer_to_peer_time 
      // console.log('端到端res',res);
      let sum = 0;
      if(res.length ===0){
        this.peer_to_peer_time = 0
      }
      else{
        for(let i=0;i<res.length;i++){
        // debugger;
        
        // console.log('ityem:',res[i]);
        sum = sum +(res[i].peer_to_peer_delay * 1);
      }
      this.peer_to_peer_time = Math.round(sum/res.length);
      }
      
      // console.log("端到端延迟数据breakRateEnd2endDelay", res);
      this.roomInfoList[2].value = this.peer_to_peer_time;
      
    },
