      // console.log('pushBitRateData11111',this.pushBitRateData);
      // let pushBitRateResList = [];
      for (const item of this.pushBitRateData) {
        const x = this.pushBitRateResList.find(r => (r.stream_id_push === item.stream_id_push))
        if (x) {
          x.list.push({
            ...item
          })
        } else {
          this.pushBitRateResList.push({
            stream_id_push: item.stream_id_push ,
            list: [{ ...item }]
