<template>
  <div class="scoring-demo">
    <!-- <router-link to="/">home</router-link> -->
    <div class="box-title">评分</div>
    <scoring
      v-model="scoring3"
      :disabled="disabled3"
      :text="scoreDetailList[0]"
      class="zhengti"
    ></scoring>
    <!-- <scoring  v-if="showmore" v-model="videoScore" :disabled="disabled3" :text="scoreDetailList[1]" class="common"></scoring>
    <scoring  v-if="showmore" v-model="funScore" :disabled="disabled3" :text="scoreDetailList[2]" class="common"></scoring>
    <scoring  v-if="showmore" v-model="demoScore" :disabled="disabled3" :text="scoreDetailList[3]" class="common"></scoring> -->
    <!-- <div style="margin-to:30px">请为本次体验打分{{scoring3}}</div>
    <div>音视频质量体验分数：{{videoScore}}</div>
    <div>功能完整性体验分数：{{funScore}}</div>
    <div>使用体验分数：{{demoScore}}</div> -->

    <!-- <div class="handle-submit-score"> -->
    <!-- <button @click="sublime3" size="min">{{disabled3 ? '取消禁用' : '提交'}}</button> -->
    <!-- `${!showIcon ? 'no-goto-btn-icon' : ''}` -->
    <div
      class="handle-submit-score"
      :class="`${isNotClick ? 'is-full-star' : 'is-not-full'}`"
      @click="handleSubmitScore(scoring3, videoScore, funScore, demoScore)"
    >
      提交 {{ $t("score_test") }}
    </div>
    <!-- </div> -->
  </div>
</template>

<script>
import scoring from "./to";
import axios from "axios";
export default {
  // name: 'scoringDemo',
  data() {
    return {
      scoreDetailList: [
        "请为本次体验打分",
        "音视频质量",
        "功能完整性",
        "使用体验",
      ],
      showmore: false,
      videoScore: 0,
      funScore: 0,
      demoScore: 0,
      isNotClick: false,
      scoring0: 0,
      scoring1: 4,
      scoring2: 2,
      scoringText2: [1, 2, 3, 4, 5],
      scoring3: 0,
      scoringText3: ["1", "2", "3", "4", "5"],
      disabled3: false,
      scoring4: 5,
      icon4: "apple",
      scoring5: 2,
      icon5: "icon5",
      voidIcon5: "voidIcon5",
      scoring6: 5,
      size6: "5rem",
      scoring7: 3,
      color7: "rgb(66, 255, 38)",
      voidColor7: "#ff2626",
      scoring8: 7,
      num8: 8,
      scoring9: 8,
      scoringText9: [
        "ABBBBBBBB",
        "AABBBBBBB",
        "AAABBBBBB",
        "AAAABBBBB",
        "AAAAABBBB",
        "AAAAAABBB",
        "AAAAAAABB",
        "AAAAAAAAB",
        "AAAAAAAAA",
      ],
      disabled9: false,
      icon9: "icon9",
      voidIcon9: "voidIcon9",
      size9: "32px",
      color9: "#9326ff",
      voidColor9: "white",
      num9: 9,
      uid: "",
      roomid: "",
      name: "",
      product: "",
      platform: "",
      appVersionStr: "",
      appVersion: 1.0,
      query: {},
    };
  },
  components: {
    // eslint-disable-next-line vue/no-unused-components
    scoring,
  },
  methods: {
    // 提交评分
    handleSubmitScore(score1, score2, score3, score4) {
      // let scoreData = {};

      // this.disabled3 = !this.disabled3
      let data = {
        uid: this.uid,
        room_id: this.roomid,
        account: "",
        name: this.name,
        client: "",
        product: this.product,
        score_verion: 1,
        score: [
          {
            index: 1,
            value: this.scoring3,
          },
          {
            index: 2,
            value: this.videoScore,
          },
          {
            index: 3,
            value: this.funScore,
          },
          {
            index: 4,
            value: this.demoScore,
          },
        ],
        common_data: {
          device_id: "",
          system_version: "",
          platform: this.platform,
          app_version: this.appVersion,
          app_version_string: this.appVersionStr,
          dist_no: 0,
        },
      };
      console.log("传过来的值score1", score1);
      console.log("传过来的值score2", score2);
      console.log("传过来的值score3", score3);
      console.log("传过来的值score4", score4);
      console.log("appVersionStr", this.appVersionStr);

      // axios.post('http://192.168.6.26:7001/scoring',data)
      axios
        .post("https://edusuite-feedback-general.zego.im/scoring", data)
        .then((response) => {
          // console.log('res=>',res);
          // console.log('提交评分成功');
          const { ret } = response.data;
          const { code, message } = ret;
          if (code === 0) {
            this.$message({
              message: "提交成功",
              type: "success",
              center: true,
              // duration:1000000
            });
          } else {
            console.error(message);
            this.$message({
              message: "提交失败，请重试",
              type: "error",
              center: true,
              customClass: "error-msg",
              // duration:1000000
            });
          }
          this.$router.push({ name: "quality-report", query: this.query });
        })
        .catch((err) => {
          console.log("err", err);
          console.log("失败！！");
        });
    },
    sublime9() {
      this.disabled9 = !this.disabled9;
      // console.log(this.scoringText9[this.scoring9 - 1])
    },
    // 监听数据
    onChange() {
      const { videoScore, funScore, demoScore } = this;
      this.$emit("change", {
        videoScore,
        funScore,
        demoScore,
      });
      // // console.log(val,oldV);
      // console.log(this.videoScore,this.funScore,this.demoScore);
      if (
        this.videoScore !== 0 &&
        this.funScore !== 0 &&
        this.demoScore !== 0
      ) {
        this.isNotClick = true;
      } else {
        this.isNotClick = false;
      }
    },
  },
  watch: {
    scoring3: function() {
      if (this.scoring3 !== 0) {
        this.isNotClick = true;
      } else {
        this.isNotClick = false;
      }
      //  if(this.scoring3<5){
      //   //  this.showmore =true;
      //    this.isNotClick = false;
      //    if(this.scoring3>=1&&this.scoring3<=4){
      //      this.videoScore= 0;
      //      this.funScore =0;
      //      this.demoScore =0;
      //    }
      //  }
      //  else{
      //   //  this.showmore =false;
      //    this.isNotClick = true;
      //    this.videoScore= 0;
      //     this.funScore =0;
      //     this.demoScore =0;
      //  }
    },
    // scoring3,videoScore,funScore,demoScore
    // scoring3: 'onChange',
    videoScore: "onChange",
    funScore: "onChange",
    demoScore: "onChange",
  },
  mounted() {
    const { query } = this.$route;
    if (query && query.uid) { 
      console.log('query',query);
    } else if(location.search){
       let search = location.search.substr(1);
       let params = search.split('&');
        params.forEach(item=>{
           if(item){
              query[item.split('=')[0]] = item.split('=')[1]
           }
        }) 
    }else{
      console.error('url formatter error!');
      return false;
    }

      this.uid = query.uid;
      this.roomid = query.room_id;
      this.name = query.name;
      this.product = query.product;
      this.platform = parseInt(query.platform);
      this.appVersionStr = query.app_version_string;
      this.appVersion = parseInt(query.app_version_string);

    this.query = query;

    query.lang && (this.$i18n.locale = query.lang);
    //  debugger;
    //  console.log('query',query);
  },
};
</script>

